.page4{
    background-color: rgb(0, 9, 29);
    width: 100vw;
    height: 100vh;
}
.contentContact{
    display: flex;
    width: 100vw;
    justify-content: space-between;
    align-items: center;
    height: 85vh;
}
.allinform2{
    text-align: start;
    text-align: justify;
    width: 30vw;
    transform: translateX(25%);
    color: white;
    font-size: 2rem;
    font-weight: bold;
}

.forma{
    display: flex;
    flex-direction: column;
    width: 45vw;
    height: 70vh;
    border: 2px solid rgb(255, 123, 0);
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    margin-right: 2rem;
}
.maincon{
    display: flex;
    flex: 3;   
}
.buttoncon{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}
.leftones{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-end;
    color: white;
}

.rightones{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
input{
    height: 2rem;
    font-size: 1.1rem;
    margin-left: 2rem;
    outline: none;
    border: none;
    border-radius: 3px;
    background-color: rgb(0, 46, 107);
    color: white;
    width: 17rem;
}
input:hover{
    border: 1px solid rgb(0, 110, 255);
    
}
.submii{
    outline: none;
    border:none;
    height: 3rem;
    width: 6rem;
    border-radius: 5px;
    font-size: 1.1rem;
    font-weight: bold;
    background-color: rgb(0, 110, 255);
    color: white;
    cursor: pointer;
}
.submii:active{
    background-color: rgb(0, 63, 146);
    color: rgb(204, 204, 204);
    -webkit-tap-highlight-color: rgba(0,0,0,0);
}
.downloadbtn{
    height: 3rem;
    width: 10rem;
    border: none;
    outline: none;
    font-size: 1.1rem;
    background-color: rgb(0, 110, 255);
    border-radius: 5px;
    color: white;
    font-weight: bold;
    cursor: pointer;
}
.downloadbtn:active{
    background-color: rgb(0, 63, 145);
}

@media screen and (max-width:960px){
    .contentContact{
        display: flex;
        flex-direction: column;
        width: 100vw;
        justify-content: space-evenly;
        align-items: center;
        height: 80vh;
    }
    .allinform2{
        text-align: start;
        text-align: justify;
        width: 90vw;
        transform: translateX(0);
        color: white;
        font-size: 1.5rem;
        font-weight: bold;
    }
    .forma{
        display: flex;
        flex-direction: column;
        width: 95vw;
        height: 50vh;
        justify-content: center;
        align-items: center;
        border-radius: 10px;
        margin-right: 0;
    }
    input{
        width: 10rem;
    }
}