.page1{
    background-image: url("../images/390704.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    max-width: 100vw;
    overflow-x: hidden;
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: space-between;
    color: white;
}

.container{
    width: 100%;
    display: flex;
    height: 100%;
    align-items: center;
    justify-content: space-between;
    background-color: rgba(0, 0, 0, 0.486);
}
.cotent1{
    transform: translateX(10vw);
    display: flex;
    flex-direction: column;
    justify-content: center;
    visibility: visible;
    animation: conten 0.5s forwards 0.4s ease;
}

img.pic1{
    width: 22vw;
    transform: translateX(-10vw);
    /*visibility: hidden;*/
    visibility: visible;
    border-radius: 10px;
    animation: imagee 0.5s forwards 0.8s ease;
}

#resume{
    color: rgb(255, 255, 255);
    background-color: rgb(0, 110, 255);
    outline: none;
    border: none;
    font-size: 1.3rem;
    height: 2.7rem;
    width: 8rem;
    border-radius: 5px;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

#resume:active{
    background-color: rgb(0, 69, 158);
}

#animm{
    animation: gradiental 1s infinite linear 0.3s;
}

@keyframes gradiental{
    from{
        background: -webkit-linear-gradient(right,  rgb(238, 255, 1), rgb(0, 238, 255));
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
    to{
        background: -webkit-linear-gradient(right,  rgb(238, 255, 1), rgb(0, 238, 255));
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
}

@media screen and (max-width:960px){

    .page1{
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        height: 100vh;
    }
    .container{
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }

    img.pic1{
        width: 70vw;    
        transform: translateX(0);
    }
    .cotent1{
        transform: translateX(0);
        margin-left: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

}