.page4{
    background-color: rgb(29, 14, 0);
    width: 100vw;
    height: 100vh;
}
.contentContact{
    display: flex;
    width: 100vw;
    justify-content: space-between;
    align-items: center;
    min-height: 100vh;
}
.allinform3{
    text-align: start;
    text-align: justify;
    width: 30vw;
    transform: translateX(25%);
    color: white;
    font-size: 2rem;
    font-weight: bold;
}
.downloadbtn{
    height: 3rem;
    width: 10rem;
    border: none;
    outline: none;
    font-size: 1.1rem;
    background-color: rgb(0, 110, 255);
    border-radius: 5px;
    color: white;
    font-weight: bold;
    cursor: pointer;
}
.downloadbtn:active{
    background-color: rgb(114, 0, 38);
}
.forma2{
    width: 50vw;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width:960px){
    .contentContact{
        display: flex;
        flex-direction: column;
        width: 100vw;
        justify-content: space-evenly;
        align-items: center;
        height: 80vh;
    }
    .allinform3{
        text-align: start;
        text-align: left;
        width: 90vw;
        transform: translateX(0);
        color: white;
        font-size: 2rem;
        font-weight: bold;
    }
    .forma2{
        width: 90vw;
        height: fit-content;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}