.page2{
    background-image: url("../images/5121.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    color: rgb(255, 255, 255);
    transition: 0.2s ease;
    
}
.content2{
    min-height: 60vh;
}
.alltables{
    margin-top: 0rem;
    width: 100vw;
    display: flex;
    justify-content: space-evenly;
}
.tabl{
    width: 30%;
    outline: 2px solid rgb(216, 216, 216);
    border-collapse: collapse;
    border-radius: 15px;
    overflow: hidden;
}
tr{
    text-align: center;
    height: 5rem;
}

th{
    color: white;
    height: 8rem;
    font-size: 1.3rem;
    width: 50%;
    font-weight: bold;
    background-color: black;
}
tr{
    background-color: rgb(43, 43, 43);
    font-weight: bold;
}

.skillline{
    width: 7.3rem;
    height: 2px;
    background-color: rgb(133, 0, 51);
    outline: none;
    border: none;
    margin: 4px 0;
    margin-bottom: 3rem;
}

@media screen and (max-width:960px){

    .page2{
        background-color: rgb(223, 223, 223);
    }
    
    .bar{
        font-size: 1.5rem;
        height: 100%;
        width: 3.8rem;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        transition: 0.1s ease;
    }
    .bar.active{
        background-color: maroon;
        transition: 0.1s ease;
    }
    .alltables{
        margin: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        
    }
    .tabl{
        width: 90%;
        margin: 0.5rem 0;
        border-radius: 8px;
        overflow: hidden;
        transform: translateY(-3vh);
    }
    tr{
        height: 3rem;
    }
    th,td{
        height: 1rem;
        width: 50%;
    }
    .titles{
        text-align: start;
        text-align: justify;
        width: 95vw;
        transform: translateX(0);
    }

}